import React from 'react'
import logo from "../assetts/image5.png"
import "./header.css"

function Header() {
    return (
        <div className="header">
            <div className="header__menu">

                <a href="/" className="header__menu__logo"><img src={logo} alt="logo"/></a>

                <div className="header__nav">
                    <nav>
                        <a href="https://animeflv.ac/anime-online.html">Animes</a>
                        <a href="https://as.com/meristation/2021/03/20/reportajes/1616262265_337829.html">TOPS ANIMES</a>
                        <a href="https://www.polygon.com/22594587/best-anime-2021">2021 TOPS</a>
                        <a href="/contacto">CONTACTO</a>

                        
                    </nav>
                </div>



            </div>
        </div>
    )
}

export default Header