import React from 'react';
import image2 from '../assetts/image2.jpg';
import image3 from '../assetts/image3.jpg';
import image4 from '../assetts/image4.jpg';
import { Carousel, Image} from 'antd';

const contentStyle = {
    height: '500px',
    color: '#fff',
    lineHeight: '600px',
    width: '1000px',
    textAlign: 'center',
    background: '#364d79',
  };


export default function carousel() {
    return (
        
        <Carousel autoplay>
            

            <div>
                <Image src={image2} style={contentStyle}></Image>
            </div>
            <div>
                <Image  src={image3} style={contentStyle}> </Image>
            </div>
            <div>
                <Image src={image4} style={contentStyle}> </Image>
            </div>

        </Carousel>
        
    )
}



